import inputBase from './input-base'
export default {
  mixins: [inputBase],
  watch: {
    value (value, oldValue) {
      // inputv-modelvalue
      // watchvaluechange
      // changevalueform-data-change
      this.onChange(value)
      if (!this.isValueChanged(value)) {
        return
      }
      // currentValue
      this.setValue(value)
    }
  },
  created () {
    // currentValue
    this.setValue(this.value)
  }
}
