import DictUtil from '../utils/util.dicts'
import log from '../utils/util.log'
/**
 * 
 */
export default {
  methods: {

    // ----------------------------
    /**
     * created
     * this._doStart()mounted
     * @private
     */
    _OnCreated () {
      this._doStart()
    },

    /**
     * 
     */
    // this.reInitColumns(crudOptions)
    /**
     *  // init
     * @private
     */
    initBefore () {
      this.crud.addRules = {}
      this.crud.addTemplate = {}
      this.crud.editRules = {}
      this.crud.editTemplate = {}
      this.crud.viewTemplate = {}
      if (this.crud.searchOptions) {
        this.crud.searchOptions.columns = []
      }
    },

    /**
     * column
     * 
     * column.sortable='custom'
     * @param item
     */
    initColumnBefore (column) {

    },
    /**
     * initColumns 
     *
     * crud-x
     * http://d2-crud-plus.docmirror.cn/d2-crud-plus/guide/mixins.html#_1-
     *
     * 
     */
    initAfter () {},

    /**
     * 
     * initColumns
     * 
     */
    doLoad () {
      return this.doRefresh({ from: 'load' })
    },

    // ----------------------------
    /**
     * D2CrudX
     * 
     * @returns
     */
    getD2Crud () {
      const ref = this.$refs[this.crud.format.ref.d2Crud]
      if (ref == null) {
        log.warn('d2Crudmounted')
      }
      return ref
    },

    /**
     *   el-table
     * @returns {*}
     */
    getD2CrudTable () {
      const ref = this.getD2Crud().$refs.elTable
      if (ref == null) {
        log.warn('el-tablemounted')
      }
      return ref
    },

    /**
     * 
     * @returns
     */
    getD2CrudTableData () {
      const d2Crud = this.getD2Crud()
      return d2Crud == null || d2Crud.d2CrudData
    },

    /**
     * 
     * @param data
     */
    setD2CrudTableData (data) {
      this.$set(this.getD2Crud(), 'd2CrudData', data)
    },

    /**
     * formData
     * 
     * @returns
     */
    getEditForm () {
      return this.getD2Crud().formData
    },

    /**
     * 
     * 
     */
    getEditRow () {
      return this.getD2Crud().formDataStorage
    },
    /**
     * [view,edit,add]
     * 
     * @returns
     */
    getEditFormMode () {
      return this.getD2Crud().formMode
    },
    /**
     * 
     * 
     * 
     * groupKey
     * @returns
     */
    getEditFormTemplate (key, groupKey) {
      return this.getD2Crud().getFormTemplate(key, groupKey)
    },
    /**
     * 
     * 
     * @returns
     */
    getEditFormTemplateGroup (groupKey, key) {
      return this.getD2Crud().getFormTemplateGroup(groupKey, key)
    },

    /**
     * ,keydict
     * @param dict  [Object:dict | string:key]
     * @param options Object {returnType: 'data'}  [data,dataMap]
     * @returns {Promise<T>}  promise
     */
    getDictData (dict, options) {
      if (typeof dict === 'string') {
        // dictdata
        dict = this.crud.columnsMap[dict].dict
      }
      // dict 
      DictUtil.mergeDefault(dict)
      return DictUtil.get(dict, options)
    },

    /**
     * ref
     * @param key
     * @returns {undefined}
     */
    getFormComponentRef (key) {
      const refs = this.getD2Crud().$refs['form_item_' + key]
      if (refs == null || refs.length === 0) {
        log.warn('')
        return
      }
      return refs[0].getComponentRef()
    },

    /**
     * 
     * no=
     */
    // this.doPageTurn (no){}

    /**
     * 
     */
    // this.doRefresh (){}

    /**
     * 
     * form = 
     */
    doSearch (form) {
      this.handleSearch(form)
    },

    /**
     * searchref
     * @returns {*}
     */
    getSearch () {
      return this.$refs[this.crud.format.ref.search]
    },

    /**
     * search form 
     */
    // this.getSearch().getForm()

    /**
     *  searchsearchform
     */
    // this.getSearch().doSearch()

    // --------------crud--------------
    /**
     * 
     */
    // this.getD2Crud().updateCell(rowIndex, key, value)

    /**
     * 
     */
    // this.getD2Crud().addRow(row)

    /**
     * 
     */
    // this.getD2Crud().handleUpdateRow(index, row)

    /**
     * 
     */
    // this.getD2Crud().removeRow(index)

    /**
     * 
     */
    // this.getD2Crud().handleLineEdit(index,row)
    /**
     * 
     */
    // this.getD2Crud().handleLineEditCancel(index,row)
    /**
     * 
     */
    // this.getD2Crud().handleLineEditSave(index,row)

    /**
     * 
     */
    // this.getD2Crud().showDialog({
    //   mode='view', //,[add,edit,view]
    //   rowIndex = 0, //
    //   template = null, //
    //   addData = null //
    //   modeContext= null //updateRequest addRequest
    //      this.getD2Crud().formModeContext modeContext
    // })

    /**
     * 
     */
    // this.getD2Crud().closeDialog()

    // ----------------------------

    /**
     * doRefresh
     */
    doAfterRefresh (query, options) {},

    /**
     * 
     *  getEditFormTemplatedoDialogOpened
     * @param context
     * @private
     */
    doDialogOpen (context) {},

    /**
     * 
     * @param context
     */
    doDialogClosed (context) {},
    /***
     * mounted
     * 
     * @param context :{
          mode: this.formMode,
          row: newRow,
          form: formData,
          template: this.formTemplateStorage,
          groupTemplate: this.formTemplateGroupStorage
      }
     */
    doDialogOpened (context) {},

    /**
     * 
     * @param done
     */
    doDialogCancel (done) {
      done() // 
    },

    /**
     * change
     * @param selection
     */
    doSelectionChange (selection) {},

    /**
     * change
     */
    doCurrentChange (currentRow, oldCurrentRow) {},

    /**
     * change
     * @param event
     */
    doSelectAll (event) {},

    /**
     * 
     * @param row
     */
    addBefore (row) {},

    /**
     * 
     * @param row
     */
    showAddMessage ({ row }) {
      this.$message({ message: '', type: 'success' })
    },

    /**
     * 
     * @param row
     */
    addAfter (row) {
      this.doAfterRowChange(row)
    },

    /**
     * 
     * @param row
     */
    editBefore (row) {},

    /**
     * 
     * @param index
     * @param row
     */
    showEditMessage ({ index, row }) {
      this.$message({ message: '', type: 'success' })
    },

    /**
     * 
     * @param row
     */
    editAfter (row) {
      this.doAfterRowChange(row)
    },

    /**
     * 
     * @param row
     */
    delBefore (row) {},

    /**
     * 
     */
    showRemoveMessage ({ index, row }) {
      this.$message({ message: '', type: 'success' })
    },

    /**
     * 
     * @param row
     */
    delAfter (row) {
      this.doAfterRowChange(row)
    },

    /**
     * 
     * 
     * 
     * @param row
     */
    doAfterRowChange (row) {
      this.doRefresh({ from: 'afterRowChange' })
    },

    /**
     * cell value 
     * @param context
     */
    doCellDataChange (context) {},
    /**
     * 
     * @param key
     * @param value
     */
    doFormDataChange ({ key, value, form }) {},

    /**
     * value
     * @param key
     * @param value
     * @param component
     * @param form
     */
    doSearchDataChange ({ key, value, component, form }) {},

    /**
     * 
     * @param columns
     */
    doColumnsFilterChanged (columns) {},

    /**
     * 
     * @param context={
          search: this.crud.searchOptions.form,
          columns: this.crud.columns,
          data: this.crud.list
     * }
     */
    doServerExport (context) {
      this.$message('doServerExport (context) window.openurl')
    },

    /**
     * 
     * @param context
     * @param columns
     * @returns {*}
     */
    doExportHeaderFormat (context, columns) {
      const columnBuilder = this.crud.pageOptions.export.columnBuilder
      return columns.map((col) => {
        const colConfig = {
          label: col.title,
          prop: col.key
        }
        if (columnBuilder) {
          columnBuilder(colConfig, context)
        }
        return colConfig
      })
    },
    /**
     *   
     * @param context
     * @param list
     * @returns {*}
     */
    doExportDataFormat (context, list) {
      const formatter = this.crud.pageOptions.export.formatter
      return list.map((row) => {
        if (formatter) {
          formatter(row, context)
        }
        return row
      })
    },

    /**
     *  ,
     * @param val
     */
    doPaginationChanged (val) {
      try {
        this.getD2CrudTable().bodyWrapper.scrollTop = 0
      } catch (e) {
      }
    }
  }
}
